"use client";

import { useMemo, useState, useRef, useEffect } from "react";
import { Package, ArrowUpDown } from "lucide-react";
import jsPDF from "jspdf";
import autoTable from "jspdf-autotable";
import { unitToKg } from "@/lib/UnitToKg";

type SortKey =
    | "name"
    | "quantity"
    | "value"
    | "tonnage"
    | "price"
    | "lowStockAt";

interface InventoryItem {
    id: string;
    quantity: number;
    lowStockAt: number;
    expiryDate?: Date | null;
    updatedAt: Date;
    product: {
        id: string;
        name: string;
        sku: string;
        price: number;
        packSize: number;
        weightValue: number;
        weightUnit: string;
        category?: { name: string } | null;
    };
    location: { name: string };
}

interface Sale {
    productId: string;
    quantity: number;
    saleDate: Date;
    locationId: string,
    product: {
        id: string;
        name: string;
        sku: string;
        price: number;
        packSize: number;
        weightValue: number;
        weightUnit: string;
        category?: { name: string } | null;
    };
    location: {
        id: string;
        name: string;
        address?: string;
    }
}

interface Props {
    inventory: InventoryItem[];
    sales: Sale[];
    title: string;
    iconColor: string;
}

export default function SalesSummary({
    inventory,
    sales = [],
    title,
    iconColor,
}: Props) {
    const [sortKey, setSortKey] = useState<SortKey>("quantity");
    const [sortDir, setSortDir] = useState<"asc" | "desc">("desc");

    const [startDate, setStartDate] = useState<Date | null>(null);
    const [endDate, setEndDate] = useState<Date | null>(null);
    const [selectedLocation, setSelectedLocation] = useState("all");
    const [selectedCategory, setSelectedCategory] = useState("all");

    const [showProducts, setShowProducts] = useState(false);
    const [selectedProducts, setSelectedProducts] = useState<string[]>([]);
    const productRef = useRef<HTMLDivElement>(null);


    /* ---------------- FILTER OPTIONS ---------------- */
    const locations = useMemo(
        () => Array.from(new Set(sales.map((i) => i.location.name))),
        [sales]
    );
    console.log(locations);
    const categories = useMemo(
        () =>
            Array.from(
                new Set(
                    sales.map((i) => i.product.category)
                )
            ),
        [sales]
    );
    console.log(categories);
    const products = useMemo(
        () =>
            Array.from(
                new Set(
                    sales.map((i) => i.product.name)
                )
            ),
        [sales]
    );
    console.log(products);

    // Close product dropdown

    useEffect(() => {
        const handler = (e: MouseEvent) => {
            if (productRef.current && !productRef.current.contains(e.target as Node)) {
                setShowProducts(false);
            }
        };
        document.addEventListener("mousedown", handler);
        return () => document.removeEventListener("mousedown", handler);
    }, []);


    /* ---------------- FILTER INVENTORY ---------------- */
    const filteredInventory = useMemo(() => {
        return inventory.filter((i) => {
            if (startDate && i.updatedAt < startDate) return false;
            if (endDate && i.updatedAt > endDate) return false;
            if (selectedLocation !== "all" && i.location.name !== selectedLocation)
                return false;
            if (
                selectedCategory !== "all" &&
                (i.product.category ?? "Uncategorized") !== selectedCategory
            )
                return false;
            if (
                selectedProducts.length > 0 &&
                !selectedProducts.includes(i.product.name)
            )
                return false;
            return true;
        });
    }, [inventory, startDate, endDate, selectedLocation, selectedCategory, selectedProducts]);

    /* ---------------- SALES MAP ---------------- */
    const salesMap = useMemo(() => {
        const map: Record<string, number> = {};
        sales.forEach((s) => {
            map[s.productId] = (map[s.productId] || 0) + s.quantity;
        });
        return map;
    }, [sales]);

    /* ---------------- AGGREGATE ---------------- */
    const data = useMemo(() => {
        const map: Record<string, any> = {};

        filteredInventory.forEach((item) => {
            const p = item.product;
            if (!map[p.id]) {
                map[p.id] = {
                    id: p.id,
                    name: p.name,
                    sku: p.sku,
                    packSize: p.packSize,
                    weightValue: p.weightValue,
                    weightUnit: p.weightUnit,
                    quantity: 0,
                    price: p.price,
                    value: 0,
                    tonnage: 0,
                    lowStockAt: item.lowStockAt,
                };
            }

            map[p.id].quantity += item.quantity;
            map[p.id].value += item.quantity * p.price;
            map[p.id].tonnage += (item.quantity * p.packSize * unitToKg(item.product.weightValue, item.product.weightUnit)) / 1000;
        });

        return Object.values(map);
    }, [filteredInventory]);

    /* ---------------- SORT ---------------- */
    const sortedData = useMemo(() => {
        return [...data].sort((a, b) => {
            const aVal = a[sortKey];
            const bVal = b[sortKey];

            // String sorting
            if (typeof aVal === "string" && typeof bVal === "string") {
                return sortDir === "asc"
                    ? aVal.localeCompare(bVal)
                    : bVal.localeCompare(aVal);
            }

            // Numeric sorting
            return sortDir === "asc"
                ? Number(aVal) - Number(bVal)
                : Number(bVal) - Number(aVal);
        });
    }, [data, sortKey, sortDir]);

    const toggleSort = (key: SortKey) => {
        if (key === sortKey) {
            setSortDir(sortDir === "asc" ? "desc" : "asc");
        } else {
            setSortKey(key);
            setSortDir("desc");
        }
    };

    /* ---------------- EXPORT CSV ---------------- */
    const exportCSV = () => {
        const headers = [
            "Product",
            "Product ID",
            "SKU",
            "Pack Size",
            "Weight Value",
            "Weight Unit",
            "Quantity",
            "Tonnage",
            "Price",
            "Value",
            "Low Stock At",
            "Status",
        ];

        const rows = sortedData.map((p) => {
            const status =
                p.quantity === 0
                    ? "Out of Stock"
                    : p.quantity < p.lowStockAt
                        ? "Low Stock"
                        : "Available";

            return [
                p.name,
                p.id,
                p.sku,
                p.packSize,
                p.weightValue,
                p.weightUnit,
                p.quantity,
                p.tonnage.toFixed(2),
                p.price.toFixed(2),
                p.value.toFixed(2),
                p.lowStockAt,
                status,
            ];
        });


        const csv = [headers, ...rows].map((r) => r.join(",")).join("\n");
        const blob = new Blob([csv], { type: "text/csv" });
        const url = URL.createObjectURL(blob);

        const a = document.createElement("a");
        a.href = url;
        a.download = "inventory_summary.csv";
        a.click();
        URL.revokeObjectURL(url);
    };

    /* ---------------- EXPORT PDF ---------------- */
    const exportPDF = () => {
        const doc = new jsPDF();
        const start = startDate ? startDate.toLocaleDateString() : "N/A";
        const end = endDate ? endDate.toLocaleDateString() : "N/A";

        doc.setFontSize(16);
        doc.text(title, doc.internal.pageSize.getWidth() / 2, 15, { align: "center" });

        doc.setFontSize(10);
        doc.text(`Date Range: ${start} - ${end}`, doc.internal.pageSize.getWidth() / 2, 23, {
            align: "center",
        });

        autoTable(doc, {
            startY: 30,
            head: [
                [
                    "#",
                    "Product",
                    "Product ID",
                    "SKU",
                    "Pack Size",
                    "Weight Value",
                    "Weight Unit",
                    "Quantity",
                    "Tonnage",
                    "Price",
                    "Value",
                    "Low Stock At",
                    "Status",
                ],
            ],
            body: sortedData.map((p, i) => {
                const status =
                    p.quantity === 0
                        ? "Out of Stock"
                        : p.quantity < p.lowStockAt
                            ? "Low Stock"
                            : "Available";

                return [
                    i + 1,
                    p.name,
                    p.id,
                    p.sku,
                    p.packSize,
                    p.weightValue,
                    p.weightUnit,
                    p.quantity,
                    p.tonnage.toFixed(2),
                    p.price.toFixed(2),
                    p.value.toFixed(2),
                    p.lowStockAt,
                    status,
                ];
            }),
            styles: { fontSize: 9, cellPadding: 4 },
            headStyles: { fillColor: [100, 100, 100], textColor: 255 },
            alternateRowStyles: { fillColor: [245, 245, 245] },
        });

        doc.save("inventory_summary.pdf");
    };

    /* ---------------- RENDER ---------------- */
    return (
        <div className="bg-white p-4 rounded-xl border hover:shadow-md transition-shadow">
            <h3 className="font-semibold mb-4 flex items-center gap-2">
                <Package className={`h-5 w-5 ${iconColor}`} />
                {title}
            </h3>

            {/* FILTER BAR */}
            <div className="flex flex-wrap justify-between items-center mb-4 gap-2">
                <div className="flex h-8 gap-5 px-2 py-1 flex-wrap">
                    <label className="flex items-center gap-2">
                        Select Location:
                        <select
                            value={selectedLocation}
                            onChange={(e) => setSelectedLocation(e.target.value)}
                            className="border rounded px-2 py-1 text-sm hover:bg-gray-100 cursor-pointer"
                        >
                            <option value="all">All</option>
                            {locations.map((loc) => (
                                <option key={loc} value={loc}>
                                    {loc}
                                </option>
                            ))}
                        </select>
                    </label>

                    <label className="flex items-center gap-2">
                        Select Category:
                        <select
                            value={selectedCategory}
                            onChange={(e) => setSelectedCategory(e.target.value)}
                            className="border rounded px-2 py-1 text-sm hover:bg-gray-100 cursor-pointer"
                        >
                            <option value="all">All</option>
                            {categories.map((cat) => (
                                <option key={cat} value={cat}>
                                    {cat}
                                </option>
                            ))}
                        </select>
                    </label>
                    <div ref={productRef} className="relative inline-block whitespace-nowrap">
                        <span className="mr-2 text-sm">Select Product:</span>

                        <button
                            onClick={() => setShowProducts((v) => !v)}
                            className="px-3 h-8 border rounded bg-white hover:bg-gray-100 text-sm"
                        >
                            {selectedProducts.length === 0
                                ? "All Products"
                                : `${selectedProducts.length} selected`}
                        </button>

                        {showProducts && (
                            <div className="absolute left-0 mt-2 bg-white border rounded shadow-lg z-50 w-64 max-h-56 overflow-y-auto p-2">

                                <button
                                    onClick={() => setSelectedProducts([])}
                                    className="text-xs text-blue-600 hover:underline mb-2 block"
                                >
                                    Clear Selection
                                </button>

                                {products.map((p) => (
                                    <label
                                        key={p}
                                        className="flex items-center gap-2 px-2 py-1 rounded hover:bg-gray-100 cursor-pointer text-sm"
                                    >
                                        <input
                                            type="checkbox"
                                            checked={selectedProducts.includes(p)}
                                            onChange={(e) => {
                                                if (e.target.checked) {
                                                    setSelectedProducts([...selectedProducts, p]);
                                                } else {
                                                    setSelectedProducts(
                                                        selectedProducts.filter((x) => x !== p)
                                                    );
                                                }
                                            }}
                                            className="w-4 h-4"
                                        />
                                        {p}
                                    </label>
                                ))}
                            </div>
                        )}
                    </div>
                </div>

                <div className="flex h-8 gap-2 px-2 py-1 flex-wrap">
                    <button
                        onClick={exportCSV}
                        className="text-sm px-2 py-1 rounded border hover:bg-gray-100"
                    >
                        Export CSV
                    </button>
                    <button
                        onClick={exportPDF}
                        className="text-sm px-2 py-1 rounded border hover:bg-gray-100"
                    >
                        Export PDF
                    </button>
                </div>
            </div>
        </div>
    )
}